const { comment } = require("postcss");

let blockLike = document.querySelector('.like__fake');
let heartLike = document.querySelector('.like__heart');
let textLike = document.querySelector('.like__text');

let countLike = textLike.innerHTML;

let secondShow = true;
blockLike.addEventListener("click", function() {
    if (secondShow)
    {
        countLike++;
       blockLike.style = "border-color: #BC9CFF";
       heartLike.style.backgroundImage = "url(data:image/svg+xml;base64,)";
       textLike.style = "color:  #BC9CFF";
       textLike.innerHTML = countLike;
       secondShow = false;
       
    }
    else
    {   countLike--;
        blockLike.style = "border-color: none";
        heartLike.style.backgroundImage = "url(data:image/svg+xml;base64,)";
        textLike.style = "color:  none";
        textLike.innerHTML = countLike;
        secondShow = true;
    }
});